<style rel="stylesheet/scss" lang="scss"  scoped>
    $baseFontSize: 192;
    .salecustomercontent{
        display: flex;
        min-height:calc(100vh - 50px);
        margin-top:  60px;
        .content{
            flex:1;
            padding: 20px;
            .contanternews{
                .dataGeneral{
                    padding: 20px 20px 0 20px;
                    .searchContent{
                        flex-wrap: wrap;
                        position: relative;
                        .bacButton{
                            position: absolute;
                            right: 0px;
                            bottom: 15px;
                        }
                        .bacButtonone{
                            right: 0px;
                            bottom: 66px;
                        }
                        .searchBox{
                            margin-right:30px;
                            margin-bottom: 20px;
                            .searchLable{
                                margin-right: 6px;
                            }
                            .serchInput{
                                width: 200px;
                                height: 30px;
                                line-height: 30px;
                                border: 1px solid #e5e5e5;
                                text-indent: 10px;
                            }
                            .searchName{
                                display: inline-block;
                                min-width: 56px;
                                line-height: 30px;
                                margin-right: 6px;
                            }
                        }
                    }
                }
                .bannerTable{
                    background: #fff;
                    margin-top: 15px;
                    padding: 10px 0 100px;
                    .noData{
                        width: 100%;
                        text-align: center;
                        margin-top: 54px;
                        color: #929292;
                        font-size: 12px;
                        .nodataImg{
                            height: 78px;
                            width: 78px;
                            margin-bottom: 16px;
                        }
                        .linkText{
                            display: block;
                            color: #929292;
                            .goHome{
                                color: #45b8c8;
                            }
                        }
                    }
                    .borderButton{
                        margin: 0 20px;
                    }
                    .table{
                        font-size: 12px;
                        border: 1px solid #f6f6f6;
                        margin: 10px  20px;
                        width: 937px;
                        tr{
                            border-bottom:1px solid #f6f6f6;
                            .line{
                                    display: inline-block;
                                    height: 8px;
                                    width: 3px;
                                    background: #eee;
                                    margin: 0 10px;
                                }
                            th{
                                font-size: 12px;
                                text-align: center;
                                background: #f6f6f6;
                                .iconshengjiangxu{
                                    font-size: 16px;
                                    margin-left: 5px;
                                }
                            }
                            th:nth-child(1){
                                max-width: 60px;
                            }
                            td:nth-child(2){
                                max-width: 70px;
                            }
                            td:nth-child(3){
                                max-width: 70px;
                            }
                            td:nth-child(4){
                                max-width: 50px;
                            }
                            td:nth-child(5){
                                max-width: 64px;
                                min-width: 64px;
                            }
                            th:nth-child(7){
                                min-width: 80px;
                            }
                            td:nth-child(10){
                                max-width: 100px;
                            }
                            th:nth-child(11){
                                min-width: 100px;
                            }
                            th:nth-child(12){
                                min-width: 120px;
                            }
                            th:nth-child(13){
                                min-width: 80px;
                            }
                            td{
                                text-align: center;
                                padding: 20px 16px;
                            }
                        }
                    }
                }
                .block{
                    text-align: right;
                    margin-top: 35px;
                    margin-right: 20px;
                }

            }
        }
    }
     .dialogone::after {
        content: "";
        display: inline-block;
        height: 100%;
        width: 0;
        vertical-align: middle;
    }
    .dialogone{
        display: none;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        text-align: center;
        z-index: 1000;
        background: rgba(0,0,0,0.5);
        .messge{
            display: inline-block;
            width: 460px;
            padding-bottom: 15px;
            vertical-align: middle;
            background-color: #fff;
            border-radius: 4px;
            font-size: 18px;
            -webkit-box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
            box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
            text-align: left;
            overflow: hidden;
            -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
            .messageHeader{
                text-align: center;
                position: relative;
                padding: 15px 15px 0px;
                color: #000;
                .messagetitle{
                }
            }
            .messageContent{
                position: relative;
                padding: 20px 30px;
                color: #606266;
                font-size: 14px;
                .messagemessage{
                    margin-bottom: 12px;
                    .inputBox{
                        border: 1px solid #e5e5e5;
                        height: 30px;
                        line-height: 30px;
                        text-indent: 10px;
                        width: 300px;
                    }
                }
            }
            .messagebtns{
                text-align: center;
                padding: 0 0 30px;
                .button{
                    border-radius: 20px;
                    display: inline-block;
                    white-space: nowrap;
                    cursor: pointer;
                    text-align: center;
                    -webkit-box-sizing: border-box;
                    box-sizing: border-box;
                    outline: 0;
                    margin: 0;
                    height: 40px;
                    width: 121px;
                    line-height: 40px;
                    font-size: 12px;
                }
                .bacButton{
                    margin-left: 20px;
                }

            }
        }
        .color2087:hover{
            color: rgba(13,13,13,0.8);
        }
    }
</style>
<template>
    <div class="general">
        <KlTop></KlTop>
        <div class="salecustomercontent contaner">
            <Aside></Aside>
            <div class="content">
                <div class="contanternews">
                    <div class="dataGeneral backWhite">
                        <div class="searchContent flex clear">
                            <div class="searchBox">
                                <span class="searchLable colorGrey font12">话题</span>
                                <input type="text" v-model="name" class="serchInput font12 colorblack" placeholder="参与话题"/>
                            </div>
                         
                            <div class="searchBox">
                                <span class="searchLable colorGrey font12">参与动态>= </span>
                                <input type="text" v-model="minPostCount" class="serchInput font12 colorblack" placeholder="参与动态"/>
                            </div>
                            <div class="bacButton cursor" @click="getcommodityData('search')">筛选 </div>
                        </div>

                    </div>
                    <div class="dataGeneral bannerTable">
                        <div class="borderButton cursor"  @click="openTopic('新增话题')">新增话题</div>
                        <table  v-if="noData" class="table">
                            <tr>
                                <th v-for="item in sortDatas" :key="item.name">
                                    {{item.name}}
                                </th>
                            </tr>
                            <tr v-for="item in tableData" :key="item.id">
                                <td>
                                    {{item.name}}
                                 </td>
                                <td>
                                    {{item.postCount}}
                                </td>
                                <td>
                                    {{item.userCount}}
                                </td>

                                <td>
                                    {{timetrans(item.createDate)}}
                              </td>
                                <td>
                                    <span class="color2087 font12 fontWeight cursor" @click="openTopic('编辑话题',item)">修改</span>
                                     <span class="line"></span>
                                     <span class="color2087 font12 fontWeight cursor" @click="godynamicmnage(item.id)">参与的动态</span>
                                     <!-- <span class="line"></span>
                                     <span class="color2087 font12 fontWeight cursor" @click="deleteTopic(item.id)">删除</span> -->
                                </td>
                            </tr>
                        </table>
                        <div v-if="!noData" class="noData">
                            <img class="nodataImg" src="../../assets/images/nodatalist.png"/>
                            <p>暂无数据~</p>
                        </div>
                        <div class="block" v-if="noData">
                            <el-pagination
                                    @size-change="handleSizeChange"
                                    @current-change="handleCurrentChange"
                                    :current-page="page"
                                    :page-size="size"
                                    :page-sizes="[5,10]"
                                    layout="total,sizes,prev, pager, next,jumper"
                                    :total="total">
                            </el-pagination>
                        </div>
                    </div>
                </div>
            </div>
            <div class="dialogone" >
                <div class="messge">
                    <div class="messageHeader">
                        <div class="messagetitle">
                            <span>{{topicTitle}}</span>
                        </div>
                    </div>
                    <div class="messageContent">
                       
                        <div class="messagemessage">
                            <span class="lableText colorblack font12">话题</span>
                            <input type="text" @change="changeValue('topName','')" v-model="topName"  class="inputBox marginLeft10 colorblack font12" placeholder="话题"/>
                        </div>
                    </div>
                    <div class="messagebtns">
                        <div class="button borderButton copyButton"  @click="closeDiologone()">
                            取消
                        </div>
                        <div class="button bacButton" @click="addTopic()">
                            确认
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Aside from '@/components/aside'
    import Service from '@/common/service'
    import KlTop from '@/components/klTop'
    import Commodities from '@/components/commodities'
    export default {
        name: "salecustomer",
        components:{
            Aside,
            KlTop,
            Commodities
        },
        data() {
            return {
                topicTitle: '',
                minPostCount: '',
                name: '',
                total: 0,
                size: 10,
                page: 1,
                tableData: [],
                noData: true,
                sortDatas:[
                    {orderType:'',name: '话题',showBlue: false,orderField: ''},
                    {orderType:'',name: '参与动态',showBlue: false,orderField: ''},
                    {orderType:'',name: '参与人数',showBlue: false,orderField: ''},
                    {orderType:'',name: '创建时间',showBlue: false,orderField: ''},
                    {orderType:'',name: '操作',showBlue: false,orderField: ''}
                ],
                topicId: '',
                topName: '',
               
            };
        },
        created(){
        },
        computed:{

        },
        watch:{
        },
        mounted(){
            this.getcommodityData('');//获取话题列表
        },
        methods: {
            //添加、编辑话题
            addTopic(){
                 if(!this.changeValue('topName','submit')){
                    return;
                }
                if(this.topicId){//编辑话题
                    Service.toppic().editorTopic({
                    name: this.topName,
                },this.topicId).then(response => {
                    if(response.errorCode == 0){
                        this.topName = '';
                        this.topicId = '';
                        this.$message.success('编辑成功')
                        this.getcommodityData('')
                        $(".dialogone").css({"display":"none"})
                    }else{
                        this.$message.error(response.message)
                    }
                }, err => {
                });
                }else{//新增话题
                    Service.toppic().addTopic({
                    name: this.topName,
                    }).then(response => {
                        if(response.errorCode == 0){
                            this.topName = '';
                            this.topicId = '';
                            this.$message.success('添加成功')
                            this.getcommodityData('')
                            $(".dialogone").css({"display":"none"})
                        }else{
                            this.$message.error(response.message)
                        }
                    }, err => {
                    });
                }

            },
            //打开编辑话题
            openTopic(type,item){
                if(item){
                    this.topicId = item.id;
                    this.topName = item.name;
                }
                this.topicTitle = type;
                $(".dialogone").css({"display":"block"})
            },
            // 跳转到动态管理
            godynamicmnage(id){
                this.$router.push({name:'dynamicmanage',query:{topicId:id}})
            },
            // 删除话题
            deleteTopic(id){
                this.$confirm( '确定删除?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    Service.toppic().deleteTopic({
                    },id).then(response => {
                        if(response.errorCode == 0){
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.getcommodityData('');
                        }else{
                            this.$message.error(response.message)
                        }

                    }, err => {
                    });
                }).catch(() => {

                });
            },
            closeDiologone() {
                this.topicId = '';
                this.topName = '';
                $(".dialogone").css({"display":"none"})
            },
           
         changeValue(name,type){
                var on = true;
              
                if(name == 'topName'){
                      this.topName =  this.topName.replace(/(^\s*)|(\s*$)/g, "");
                    if(this.topName.length>20 ||  this.topName.length == 0){
                        this.$message.error('请输入不超过20个字符的话题');
                        on = false;
                        return;
                    }
                }
            
                if(type=='submit'){
                    return on;
                }
            },
           
            handleSizeChange(val) {
                this.size = val;
                this.getcommodityData('')
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getcommodityData('')
            },
            timetrans(timestamp) {
                var getSeconds = '', getMinutes = '', getHours = '';
                var d = new Date(timestamp);
                getHours = d.getHours() < 10 ? '0' + d.getHours() : d.getHours();
                getMinutes = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes();;
                getSeconds = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds();;
                var newTime = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + getHours + ':' + getMinutes + ':' + getSeconds;
                return newTime
            },
       
            getcommodityData(str){
                if(str == 'search'){
                    this.page = 1;
                }
                Service.toppic().gettopicList({
                    minPostCount: this.minPostCount,
                    "page": this.page,
                    "name": this.name,
                    "size": this.size,
                }).then(response => {
                    if(response.errorCode == 0){
                        if(response.data.records.length == 0){
                            this.noData = false;
                        }else{
                            this.noData = true;
                            this.total = response.data.total;
                            this.$nextTick(()=>{
                                this.tableData = response.data.records;
                            })
                        }
                    }else{
                        this.$message.error(response.message)
                    }
                }, err => {
                });

            },
        },

    }
</script>




